.palettes {
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: gray; }
  .palettes__list {
    display: flex;
    flex-wrap: wrap;
    list-style: none; }
  .palettes .palette {
    padding-right: 1rem; }
    .palettes .palette__title {
      font-size: 1.2rem; }
    .palettes .palette__colors {
      margin-bottom: 1rem;
      height: 3rem;
      box-sizing: content-box;
      border: solid 0.4rem #ccc;
      cursor: pointer; }
    .palettes .palette__color {
      display: inline-block;
      width: 3rem;
      height: 3rem; }
    .palettes .palette--favorite .palette__colors {
      border-color: #f00; }
    .palettes .palette--hidden {
      opacity: 0.3; }
