.palettes {
    margin-bottom: 1rem;
    padding: 1rem;
    background-color: gray;
  
    &__list {
      display: flex;
      flex-wrap: wrap;
      list-style: none;
    }
  
    .palette {
      padding-right: 1rem;
  
      &__title {
        font-size: 1.2rem;
      }
  
      &__colors {
        margin-bottom: 1rem;
        height: 3rem;
        box-sizing: content-box;
        border: solid 0.4rem #ccc;
        cursor: pointer;
      }
  
      &__color {
        display: inline-block;
        width: 3rem;
        height: 3rem;
      }
  
      // modifiers
  
      &--favorite .palette__colors {
        border-color: #f00;
      }
  
      &--hidden {
        opacity: 0.3;
      }
    }
  }